<template>
  <div class="about" style="min-height:85vh;">
    <div class="sub_menu">
      <el-row>
        <el-col :span="10"><div class="grid-content"></div></el-col>
        <el-col :span="4"><div class="grid-content">
          <el-button type="success" round v-on:click="gotoBefore">システムA</el-button>
          <el-button type="info" round @click="logout">logout</el-button>
        </div></el-col>
        <el-col :span="10"><div class="grid-content"></div></el-col>
      </el-row>
    </div>
    <div class="space"/>
    <div>
      <div class="twitcharea_bf">
        <iframe src="https://player.twitch.tv/?channel=it0sat0&parent=it0sat0.github.io" 
        frameborder="0" allowfullscreen="true" scrolling="no" height="567" width="930"></iframe>
        <div>
          <iframe
            id="chat_embed"
            src="https://www.twitch.tv/embed/it0sat0/chat?parent=it0sat0.github.io"
            height="500" width="300">
          </iframe>
          <el-row>
            <el-col :span="24">
              <el-input placeholder="Input command" v-model="command" @keypress.enter.native="post">
              <el-button slot="append" @click="post">Chat</el-button>
              </el-input>
            </el-col>
          </el-row>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import firebase from 'firebase'
  import axios from "axios"
  import tmi from "tmi.js"

  export default {
    data() {
      return {
        channelName: this.$_getchannel(),
        username: this.$_getusername(),
        password: this.$_getpassword(),
        channelURL: ``,
        email: this.$_getuseremail(),
        ChatCommand: '',
        command: '',
        timeoutTimer: ''
      }
    },
    created() {
      this.channelURL = `https://player.twitch.tv/?channel=${this.channelName}&parent=it0sat0.github.io`
      console.log("channelURL:"+this.channelURL)
    },
    mounted: function(){
      // 初期化処理
      var obj=document.getElementById("ifr")
      obj.removeAttribute("src")
      document.getElementById("ifr").src = this.channelURL
      this.timeout()
    },
    methods: { 
      gotoBefore(){
        this.$router.push('/about')
      },
      post(){
        clearTimeout(this.timeoutTimer)
        console.log("OK command:" + this.command)
        this.sendCommand(this.command)
      },
      sendCommand(command){
        /*
        const opts = {
          channels: [this.channelName],
          identity: {
            username: this.username,
            password: this.password 
          }
        };
        */
       /*
        const opts = {
          channels: ["it0sat0"],
          identity: {
            username: "it0sat0sub",
            password: "oauth:3rl5k8t3wz5q4brrex0alg0efbr1h2"
          }
        };
        */
        const opts = {
          channels: ["it0sat0sub2"],
          identity: {
            username: "it0sat0sub3",
            password: "oauth:w0bcb3fazr9bne37u8gtlhl8mdbcc4"
          }
        };

        // Create a client with our options
        const client = new tmi.client(opts);

        client.connect();

        // Register our event handlers (defined below)
        client.on('connected', onConnectedHandler);

        function onConnectedHandler (addr, port) {
          console.log(`* Connected to ${addr}:${port}`);
          client.say('it0sat0', `${command}`)
        }
        this.command = ""
        this.timeout()
      },
      func() {
       console.log(this.channelURL)
      },
      timeout: function(){
        this.timeoutTimer = setTimeout(() => {
          alert("一定時間操作がなかったため、ログアウトします")
          clearTimeout(this.timeoutTimer)
          this.logout()
          }
          ,600000 //時間変更可能
        )
      },
      check: function(){
        console.log(this.channelURL)
        var obj=document.getElementById("ifr")
        obj.removeAttribute("src")
        document.getElementById("ifr").src = this.channelURL
      },
      logout: function(){
        clearTimeout(this.timeoutTimer)
        this.logouotRequest(this.email)
        firebase.auth().signOut();
        clearTimeout(this.timeoutTimer)
        this.$router.push('/')
      },
      logouotRequest: async function(username){
        const params = new URLSearchParams();
        params.append("name",username);
        await axios.post("http://localhost:8040/logout", params)
        //await axios.post("https://asia-east1-twitch-MCI.cloudfunctions.net/logout", params)
        alert("ログアウトしました")
      }
    }
  }
</script>

<style>
