  margin: 1em;
}

.twitcharea_bf{
  display:flex;
  justify-content: center;
}

.wasd{
  margin: 10px;
}

.wasd-list{
  display: flex;
  justify-content: center;
}

.wasd-text{
  width: 5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.chest{
  margin: 10px;
  justify-content: center;
  align-items: center;
}
</style>
